*{
  background-color: rgb(170, 105, 125);
  margin: 0%;
}
.cuerpo{
  width: 90%;
  height: auto;
  /*border: palegreen 2px solid;*/
  margin-top: 20px;
  margin-left: 65px;
  display: flex;
  flex-wrap: wrap;
}

.unidad{
  width: 600px;
  height: 400px;
  margin: 30px;
  border: crimson 2px solid;
  text-align: center;
  background-color: white;
}
p{
  font-size: 1.5rem;
  background-color: white;
}
a{
  text-decoration: none;
  color: black;
}
img{
  width: 600px;
  height: 300px;
}

.menu{
  list-style: none;
  display: flex;
}
li{
  margin-right: 10px;
  border: black 1px solid;
}
.contenido{
  width: 90%;
  margin-left: 5%;
  margin-top: 50px;
  height: auto;
  background-color: white;
  text-align: justify;
  padding: 10px;
}
.pie{
  width: 100%;
  height: 50px;
  margin-top: 30px;
  background-color: white;
}